















import { Component, Prop, Vue } from 'vue-property-decorator'
import { eventBus } from '../../../main'

@Component
export default class SelectCity extends Vue {
  cities = [
    'Москва',
    'Калининград',
    'Екатеринбург',
    'Санкт-Петербург',
    'Пермь',
    'Киров',
    'Киев',
    'Алматы'
  ];

  selectCity (cityName: string) {
    eventBus.$emit('select-city', cityName)
  }
}
